.App {
  display: flex;
  flex-direction: column;
  flex: 1;

  height: 100vh;
}

.tab-bar {
  display: flex;
  justify-content: space-around;
  flex: 1;
  background-color: blue;
}

.selection-area {
  display: flex;

  flex: 10;
}
.selection-area > div {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  flex: 1;
  background-color: #f82d29;
}

.footer img {
  width: 1.3rem;
  height: 1.3rem;
}

.footer div {
  flex: 1;
  color: white;
  text-align: center;
  margin: 0 2rem;
  border-left: solid 0.15rem #f66e6e;
  border-right: solid 0.15rem #f66e6e;
}
